.ContactLayout {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    /*align-items: center;*/
    justify-content: space-evenly;
}

.ListGroup {
    padding-bottom: 10px;
}

.GroupItemHeader {
    /*background-color: #F7F7F7 !important;*/
    font-size: 2.5vh;
}

.GroupItem > * {
    font-size: 2vh !important;
}

.moreInfoBtn {
    float: right;
    margin-left: 10px;
    font-weight: bold;
}

.moreInfoBtn:hover {
  background-color: #C1B7A7 !important;
  color: #44403A !important;
}

.InfoLayout {
    /*height: 350px !important;*/
    /*min-width: 330px;*/
    height: 500px !important;
    border: solid 1px rgba(0, 0, 0, 0.125);
    border-radius: 0.25rem;
}

.InfoLayout > * {
    height: 100%;
    max-width: 85vw !important;
}

.MailFormLayout {
    min-width: 50vh;
    min-height: 100% !important;
    height: auto !important;
    background-color: #F4E7D3;
    margin-bottom: 3rem;
    border-radius: 0.25rem;
}

.MailForm  > * {
    padding: 15px;
    margin-bottom: 0 !important;
}

.MailToolbar > * {
    margin: 10px;
}